/*Contexto.css*/
html, body {
  height: auto;
  min-height: 100%;
  overflow-y: auto;
}
.contexto-container {
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(to right, #013b4b, #032043); /* Gradient */
  padding: 2rem;
  text-align: center;
  color: #f5f5f5;
  height: 100vh; /* Ensure full height */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: auto; /* Enable scrolling */
}


.contexto-input {
  padding: 12px 16px;
  font-size: 16px;
  border-radius: 6px;
  border: none;
  width: 280px;
  margin-bottom: 12px;
  margin-right: 8px;
}

.button-row {
  display: flex; /* Make the row a flex container */
  align-items: center;
  justify-content: space-between; /* Distribute the items evenly with space between */
  width: 100%; /* Ensure it takes full width */
}

button {
  padding: 10px 18px;
  margin: 0 6px;
  font-size: 15px;
  border-radius: 6px;
  border: none;
  background-color: #4087c1;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: #2c6ca3;
}

.answer-box {
  margin-top: 20px;
  font-size: 18px;
  background-color: #f2f2f2;
  padding: 10px 20px;
  border-radius: 6px;
  text-align: center;
}

.guesses-container {
  margin-top: 20px;
}

.guess-box-wrapper {
  width: 300px; /* Matches .contexto-input */
  margin: 8px auto;
  background-color: #333;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}
.guess-box-content {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  color: white;
  font-weight: 600;
}

.guess-box-filled {
  height: 100%;
  display: flex;
  align-items: center;
  transition: width 0.3s ease;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  border-radius: 6px;
  z-index: 1;
}

.guesses-count {
  font-size: 18px;
  color: #fff;
  margin-bottom: 20px;
}

.guess-word {
  text-align: left;
  flex: 1;
}

.guess-rank {
  text-align: right;
  flex-shrink: 0;
  margin-left: 10px;
}

.back-button {
  margin-top: 30px;
  background-color: #588fd1;
}

.back-button:hover {
  background-color: #333;
}

/* Popup Styles */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.popup-box {
  background: #032043;
  color: #4087c1;
  padding: 2rem 3rem;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0,0,0,0.3);
  text-align: center;
  max-width: 300px;
  width: 60%;
}

.popup-box h2 {
  color: #4087c1;
  margin-bottom: 1rem;
}

.popup-box button {
  margin-top: 1rem;
  background-color: #4087c1;
  color: white;
  border: none;
  padding: 10px 18px;
  border-radius: 6px;
  font-size: 15px;
  cursor: pointer;
}

.popup-box button:hover {
  background-color: #2c6ca3;
}

.bolt {
  position: absolute;
  width: 40px; /* Increased size */
  height: 40px;
  background: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='%23FFFF00' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7 2L17 12H10L17 22L7 12H14L7 2Z'/%3E%3C/svg%3E") no-repeat center;
  background-size: contain;
  opacity: 0.5;
  z-index: 0;
  top: -60px;
  animation-name: fall-bolt;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  pointer-events: none;
}

@keyframes fall-bolt {
  0% {
    transform: translateY(0) rotate(0deg) scale(1);
    opacity: 0.6;
  }
  100% {
    transform: translateY(120vh) rotate(360deg) scale(1.2);
    opacity: 0;
  }
}
